@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
  
  .chart {
    background: transparent;
    border-radius: 10px;
    color: white;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
  }
  
  .candle {
    stroke-width: 1;
  }
  
  .candle.up {
    fill: #81dfc4;
    stroke: #81dfc4;
  }
  
  .candle.down {
    fill: #d75b6d;
    stroke: #d75b6d;
  }
  
  .wick {
    stroke-width: 1.5;
  }
  
  .wick.up {
    stroke: #81dfc4;
  }
  
  .wick.down {
    stroke: #d75b6d;
  }
  
  .cross_hair {
    stroke: white;
    stroke-opacity: 70%;
    stroke-dasharray: 3;
  }
  
*{

  font-family:Montserrat,sans-serif;
}
.center{
  margin: 0 auto;
  width:80% /* value of your choice which suits your alignment */
}

.message_wrap {
  height:350px;
  padding: 1px;
}
.message_box {
  font-size: large;
  color: azure;
  border:1px solid rgb(0, 0, 0);
  background-color: #19471d;
  padding: 10px;
  width:auto;
